import React, {Component} from 'react';
import ReactDOM from "react-dom";
import $ from 'jquery';
import '../../bower_components/jquery.countdown/dist/jquery.countdown.js';


class BlackfridayWidget extends Component {
    constructor() {
        super();

    }
    componentDidMount(){

        $("#getting-started")
            .countdown("2017/11/28", function(event) {
                $(this).text(
                    event.strftime('%d %H:%M:%S')
                );
            });
    }

    render() {

        return (
            <div>
                <div className="black mb0">
                    <div className="container mb0">
                        <div className="row mb0 p-40">
                            <div className="col s12 m12 l3 center">
                                <br />
                                <img src="./wow.png" alt="wow face" className="responsive-img"/>
                            </div>
                            <div className="col s12 m12 l6 center">
                                <h4 className="white-text">
                                    CYBER MONDAY BLOWOUT! <br/>
                                    ALL TICKETS 50% OFF!!!
                                </h4>
                                <h5 className="white-text text-normal">
                                    <strike className="red-text">$997</strike> $497 VIP 2 DAY TICKETS BEFORE SALE ENDS
                                </h5>
                                <h5 className="white-text text-normal">
                                    <strike className="red-text">$497</strike> $247 REGULAR TICKETS BEFORE SALE ENDS
                                </h5>
                                <h5 className="white-text text-uppercase text-normal">
                                    USE CODE: "cybermonday" at checkout!!!
                                </h5>
                            </div>
                            <div className="col s12 m12 l3 center">
                                <br />
                                <br />
                                <a href="https://www.eventbrite.com/e/eric-thomas-fownders-presents-take-ownership-tickets-39204971124" className="btn btn-large w100 red">GET TICKETS</a>
                                <h6 className="white-text">TIME LEFT: <span className="white-text text-uppercase" id="getting-started"></span></h6>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )

    }

}

export default BlackfridayWidget;



